@import url(https://fonts.googleapis.com/css?family=Barriecito&display=swap);
h3 {
  font-family:  'Barriecito', cursive;
  font-size: 50px;
  color: rgb(52, 54, 53);
  margin: 0 auto;
}
h3::first-letter{
  font-size: 63px;
}
.header {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  margin-right: 15vh;
  margin-top: 10vh;
}
.dude {
  width: 160px;
  height: auto;
}

.show-item img {
    margin: 2vh;
}
.preview h4 {
    border-bottom: 1px solid #999;
    padding-bottom: .5vh;
    font-size: 30px;
    font-weight: normal;
    margin: 0 0 2vh 0;
    text-align: left;
}
.preview-info span{
    margin-bottom: .6vh;
}

.preview-info {
    display: flex;
    flex-direction: column;
    padding: 0 3vh;
    
}
ul li {
    list-style: none;
}
.term {
    font-weight: 600;
}
.preview {
    font-family: Georgia, 'Times New Roman', Times, serif;
    border: 1px solid #c1c0c085;
    box-shadow: 0px 0px 26px -10px rgba(0,0,0,0.75);
    padding: 2vh;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    -webkit-animation: fadeIn .5s ease-in-out;
            animation: fadeIn .5s ease-in-out;
    margin: 3vh 0;
}
.preview-show-item {
    margin: 0;
}
.hide-preview-show, .back-preview-show{
    cursor: pointer;
    position: absolute;
    font-size: 30px;
    top: 35%;
    color: #333;
}
.hide-preview-show{
    right: 7%;
}
.back-preview-show{
    left: 7%;
}
@-webkit-keyframes fadeIn {
    from {
      opacity: 0;
    }
  
    to {
      opacity: 1;
    }
  }
@keyframes fadeIn {
    from {
      opacity: 0;
    }
  
    to {
      opacity: 1;
    }
  }
  
@keyframes lds-eclipse {
    0% {
      -webkit-transform: rotate(0deg);
      transform: rotate(0deg);
    }
    50% {
      -webkit-transform: rotate(180deg);
      transform: rotate(180deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }
  @-webkit-keyframes lds-eclipse {
    0% {
      -webkit-transform: rotate(0deg);
      transform: rotate(0deg);
    }
    50% {
      -webkit-transform: rotate(180deg);
      transform: rotate(180deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }
  .lds-css{
    width: 100%;
    height: 12vh;
    background-color: #303030;
    margin-bottom: 2vh;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #444;
  }
  .lds-eclipse {
    
    position: relative;
  }
  .lds-eclipse div {
    position: absolute;
    -webkit-animation: lds-eclipse 1.1s linear infinite;
    animation: lds-eclipse 1.1s linear infinite;
    width: 160px;
    height: 160px;
    top: 20px;
    left: 20px;
    border-radius: 50%;
    box-shadow: 0 4px 0 0 #8d9d97;
    -webkit-transform-origin: 80px 82px;
    transform-origin: 80px 82px;
  }
  .lds-eclipse {
    width: 73px !important;
    height: 73px !important;
    -webkit-transform: translate(-36.5px, -36.5px) scale(0.365) translate(36.5px, 36.5px);
    transform: translate(-36.5px, -36.5px) scale(0.365) translate(36.5px, 36.5px);
  }

body{
  margin: 0;
  padding: 0;
  background: rgb(230, 230, 230);
}
.container{
    margin: 0 auto;
    width: 80%;
}
.show-item {
  width: 271px;
  max-height: 420px;
  border: 1px solid #c1c0c085;
  box-shadow: 0px 0px 26px -10px rgba(0,0,0,0.75);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 2vh;
  cursor: pointer;
}
.show-item span{
  font-size: 20px;
  margin: .3vh;
  width: 80%;
}
.item-list{
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}
.search-input {
  width: 40vh;
  height: 30px;
}
.search {
  display: flex;
  justify-content: center;
}

.search input {
  font-size: 20px;
  background: rgba(255, 255, 255, 0.473);
  border: none;
}
.info-btn{
  position: fixed;
  top: 2%;
  left: 2%;
  background: #fff;
  border-radius: 50%;
  border: 1px solid #999;
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.show-casts {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  -webkit-animation: fadeIn .5s ease-in-out;
          animation: fadeIn .5s ease-in-out;
}
.cast-show-item img {
  width: 70%;
  margin: 1vh;
}
.cast-show-item span {
  width: 70%;
  margin: .3vh;
}
.cast-show-item {
  width: 171px;
  min-height: 240px;
  border: 1px solid #c1c0c085;
  box-shadow: 0px 0px 26px -10px rgba(0,0,0,0.75);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 2vh;
}
@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}
